
audio{display:none
}
.vux-1px,.vux-1px-t,.vux-1px-b,.vux-1px-tb,.vux-1px-l,.vux-1px-r{position:relative
}
.vux-1px:before{content:" ";position:absolute;left:0;top:0;width:200%;height:1px;border:1px solid #C7C7C7;color:#C7C7C7;height:200%;transform-origin:left top;transform:scale(.5)
}
.vux-1px-t:before{content:" ";position:absolute;left:0;top:0;right:0;height:1px;border-top:1px solid #C7C7C7;color:#C7C7C7;transform-origin:0 0;transform:scaleY(.5)
}
.vux-1px-b:after{content:" ";position:absolute;left:0;bottom:0;right:0;height:1px;border-bottom:1px solid #C7C7C7;color:#C7C7C7;transform-origin:0 100%;transform:scaleY(.5)
}
.vux-1px-tb:before{content:" ";position:absolute;left:0;top:0;right:0;height:1px;border-top:1px solid #C7C7C7;color:#C7C7C7;transform-origin:0 0;transform:scaleY(.5)
}
.vux-1px-tb:after{content:" ";position:absolute;left:0;bottom:0;right:0;height:1px;border-bottom:1px solid #C7C7C7;color:#C7C7C7;transform-origin:0 100%;transform:scaleY(.5)
}
.vux-1px-l:before{content:" ";position:absolute;left:0;top:0;width:1px;bottom:0;border-left:1px solid #C7C7C7;color:#C7C7C7;transform-origin:0 0;transform:scaleX(.5)
}
.vux-1px-r:after{content:" ";position:absolute;right:0;top:0;width:1px;bottom:0;border-right:1px solid #C7C7C7;color:#C7C7C7;transform-origin:100% 0;transform:scaleX(.5)
}
#audiobox{width:90%;height:50px;margin:10px auto 20px auto
}
#audiobox #audioplayer{height:100%;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center
}
#audiobox #audioplayer #pButton{height:40px;width:40px;border:none;background-size:50% 50%;background-repeat:no-repeat;background-position:center;float:left;outline:none;position:relative;z-index:10001
}
#audiobox #audioplayer .controls{font-family:'Roboto',sans-serif;font-size:16px;line-height:18px;color:#55606E;display:-ms-flexbox;display:flex;-ms-flex-positive:1;flex-grow:1;-ms-flex-pack:justify;justify-content:space-between;-ms-flex-align:center;align-items:center;margin-left:.2rem;margin-right:24px
}
#audiobox #audioplayer .controls span{cursor:default
}
#audiobox #audioplayer .controls .slider{margin-left:16px;margin-right:16px;border-radius:2px;height:4px;-ms-flex-positive:1;flex-grow:1;background-color:#D8D8D8;cursor:pointer;position:relative
}
#audiobox #audioplayer .controls .slider .progress{width:0;height:100%;background-color:#f2cd4b;border-radius:inherit;position:absolute;pointer-events:none
}
#audiobox #audioplayer .controls .slider .progress .pin{height:14px;width:14px;border-radius:8px;background-color:#f2cd4b;position:absolute;pointer-events:all;box-shadow:0 1px 1px 0 rgba(0,0,0,0.32);right:-8px;top:-6px
}
.play{background:url('http://www.alexkatz.me/codepen/img/play.png')
}
.pause{background:url('http://www.alexkatz.me/codepen/img/pause.png')
}


