.comment-title{line-height:.9rem;width:90%;margin:auto;font-size:.3rem;color:#a8a8a8
}
#comment{width:90%;margin:auto;position:relative;margin-bottom:30px
}
#comment li{padding:.34rem 0;display:block;height:auto;position:relative;overflow:hidden
}
#comment li img{float:left;display:block;width:.68rem;height:.68rem;border-radius:.34rem
}
#comment .comment-box{width:70%;margin-left:.26rem;float:left;font-size:.2rem
}
#comment .comment-box .username{font-size:.32rem;line-height:.5rem
}
#comment .comment-box .date{font-size:12px;line-height:.3rem;color:#888888
}
#comment .comment-box .comment-con{margin-top:.1rem;font-size:.28rem;line-height:.4rem;color:#9e9d9d
}
#comment .comment-box .zan{font-size:#949494;position:absolute;right:.34rem;top:.34rem
}
#comment .comment-box .zan .icon img{width:16px;height:16px
}
#comment .comment-box .zan .count{margin-left:10px;color:#a8a8a8
}
.comment-none{text-align:center;color:#666;font-size:14px;line-height:50px
}


